	},
	methods: {
		fnWeiXin () {
			uni.login({
				provider: 'weixin',
				success: (res) => {
					uni.getUserInfo({
						provider: 'weixin',
						success: (infoRes) => {
							/**
							 * 实际开发中，获取用户信息后，需要将信息上报至服务端。
							 * 服务端可以用 userInfo.openId 作为用户的唯一标识新增或绑定用户信息。
							 */
							uni.showToast({
								title: JSON.stringify(infoRes.userInfo),
								icon: 'none',
								duration: 5000
							})
						},
						fail() {
							uni.showToast({
								icon: 'none',
								title: '登陆失败'
							});
						}
					});
				},
				fail: (err) => {
					console.error('授权登录失败：' + JSON.stringify(err));
				}
			});
		},
